    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6); 
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity 0.3s ease-in-out;
  }
  
  /* PDF body styling */
  .Pdf_Body {
    background-color: white;
    padding: 20px;
    width: 80%;
    max-height: 80%;
    overflow-y: auto;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    z-index: 1001;
    position: relative;
  }
  
  /* Fade-in effect */
  .overlay.hidden {
    opacity: 0;
    pointer-events: none;
  }
  
